<script>
import SampleExpression from "./expression";
export default {
  name: "sampleRateCal",
  components: {
    SampleExpression,
  },
  data() {
    return {
      formLayout: "horizontal",
      form: this.$form.createForm(this, { name: "coordinated" }),
      ua: 1.6449,
      up: 1.2816,
      result: "",
    };
  },
  methods: {
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          this.calculate(values);
        }
      });
    },
    calculate({ pe, pc, k } = {}) {
      const { ua, up } = this;
      const p = (pe + pe * pc) / (1 + pe);
      const molecular = Math.pow(ua + up, 2) * (1 + 1 / k) * p * (1 - p); // ÂÖ¨ÂºèÁöÑÂàÜÂ≠ê
      const denominator = Math.pow(pe - pc, 2); // ÂÖ¨ÂºèÁöÑÂàÜÊØç
      this.result = molecular / denominator;
    },
  },
};
</script>
